const useStyles = makeStyles((theme) => ({
  secondaryTail: {
    backgroundColor: theme.palette.secondary.main,
  },
  defaultPrimary : {
    '& > *': {
      backgroundColor: 'green',
    },
  },
}));

function OrderTimeline(props) {
  const classes = useStyles();
  const {row} = props;
  return (
    <Timeline align="alternate">
      <TimelineItem>
        <TimelineSeparator>
          <TimelineDot color={row.deliveryAssigned ? 'primary' : 'grey'}> <PersonIcon/></TimelineDot>
          <TimelineConnector/>
        </TimelineSeparator>
        <TimelineContent><Paper variant='outlined'><div className='txtBox'>

          
            <div className='delName'> {row.deliveryAssigned ? row.assignedToWhom[0] : 'Your Delivery'}</div>
            
           
              <div className='delNameTxt'>{row.deliveryAssigned ? 'will do your Delivery' : 'is not Assigned Yet'}</div>
             
         </div> </Paper></TimelineContent>
      </TimelineItem>
      <TimelineItem>
        <TimelineSeparator>
        <TimelineDot color={row.deliveryAssigned ? 'primary' : 'grey'}><CallIcon onClick={()=>row.deliveryAssigned && window.open('tel:'+row.assignedToWhom[1], '_self')} /> </TimelineDot>
          <TimelineConnector/>
        </TimelineSeparator>
        <TimelineContent><Paper variant='outlined'><div className='txtBox'  onClick={()=> row.deliveryAssigned && window.open('tel:'+row.assignedToWhom[1], '_self')}> 

           
            <div className='delMob'> {row.deliveryAssigned ? row.assignedToWhom[1] : ' '}</div>
            
            
             <div className='delMobTxt'>{row.deliveryAssigned ? ' Tap To Call' : 'You can call them once assigned'}</div>
           

        </div> </Paper></TimelineContent>
      </TimelineItem>
      <TimelineItem>
        <TimelineSeparator>
          <TimelineDot color={row.startTime==='Not Started' ? 'grey' : 'secondary'}><AccessTimeIcon/> </TimelineDot>
           <TimelineConnector className={classes.secondaryTail}/>
        </TimelineSeparator>
        <TimelineContent><Paper variant='outlined'><div className='txtBox'>

          
              <div className='delStart'>Starting Time</div>
           
            
             <div className='delStartTxt'>{row.startTime==='Not Started' ? 'Not Started Yet' : row.startTime}</div>
           

         </div> </Paper></TimelineContent>
      </TimelineItem>
      <TimelineItem>
        <TimelineSeparator>
          <TimelineDot color={row.endTime==='Not Started' ? 'grey' : 'secondary'}><AlarmIcon/> </TimelineDot>
        </TimelineSeparator>
        <TimelineContent><Paper variant='outlined'><div className='txtBox'>

           
              <div className='delEnd'>Ending Time</div>
            
             
             <div className='delEndTxt'>{row.endTime==='Not Started' ? 'Not Started Yet' : row.endTime}</div>
            


         </div> </Paper></TimelineContent>
      </TimelineItem>
    </Timeline>
  );
}

const useRowStyles = makeStyles({
   table: {
    minWidth: 320,
  },
  root: {
    '& > *': {
      borderBottom: 'unset',

    },
  },
  colorPrimary : {
    '& > *': {
      color: 'green',
    },
  }
});


function RowEarning(props) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();

  return (
    <React.Fragment>
      <TableRow className={classes.root} onClick={() => setOpen(!open)}>
        <TableCell component="th" scope="row">
          {row.item_name}
          <br/><span className='expandTxt'>{!open ? <ExpandMoreIcon/> : <ExpandLessIcon/> }</span>
        </TableCell>
        <TableCell align="left">  {row.amount}</TableCell>
        <TableCell align="left">{row.forHours}</TableCell>
        <TableCell align="left">{row.payment_status ? <CheckBoxIcon color='primary' className={classes.colorPrimary}/> : <ReportProblemIcon color='error'/>}</TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={0}>
              
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell>On Date</TableCell>
                    <TableCell align="left">Vehicle Number</TableCell>
                    
                  </TableRow>
                </TableHead>
                <TableBody>
                    <TableRow key={row.item_name}>
                      <TableCell component="th" scope="row">
                        {row.onDate}
                      </TableCell>
                    
                    
                      <TableCell align="left">{row.item_regno}</TableCell>
                    </TableRow>
                 
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}


function RowHistory(props) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();

  return (
    <React.Fragment>
      <TableRow className={classes.root}  onClick={() => setOpen(!open)}>
        
        <TableCell component="th" scope="row">
          {row.item_name}
          <br/><span className='expandTxt'>{!open ? <ExpandMoreIcon/> : <ExpandLessIcon/> }</span>
        </TableCell>
   
        <TableCell align="right">  {row.amount}</TableCell>
        <TableCell align="right">{row.forHours}</TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={1}>
              
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell>On Date</TableCell>
                    <TableCell align="left">Start</TableCell>
                    <TableCell align="left">End</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                    <TableRow key={row.item_name}>
                      <TableCell component="th" scope="row">
                        {row.onDate}
                      </TableCell>
                    
                    
                      <TableCell align="left">{row.startTime}</TableCell>
                      <TableCell align="left">
                        {row.endTime}
                      </TableCell>
                    </TableRow>
                 
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}



function Row(props) {
  const { row } = props;
  const classes = useRowStyles();

  return (
    <React.Fragment>
      <TableRow className={classes.root} >
        <TableCell component="th" scope="row">
          {row.item_name}
        </TableCell>
        <TableCell align="left">  {row.amount}</TableCell>
        <TableCell align="left">{row.for_hours}</TableCell>
        <TableCell align="left">{row.extra_charges}</TableCell>
        
      </TableRow>
    </React.Fragment>
  );
}

  let user = {  username : ls.get('username'),
        uniqueid : ls.get('uniqueid'),
        ssto : ls.get('ssto'),
            tokenId: ls.get('tokenId'),
            t2 : ls.get('t2'),
        }



 const serverRequestPost = async (reqType) =>  {
          
          return await axios.post(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php`,
          stringify({ [reqType]:'',
                      tokenId : user.tokenId,
                      t2 : user.t2,
                      username : user.username,
                      uniqueid : user.uniqueid,
                      ssto : user.ssto

                       }))
       
        .then((response) =>{
                return response.data.success ? response.data : "Request Failed";
                console.log(response.data);
               
      });

}

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}



function UserProfile(props) {
  return( <div className='Profile'>
                <div className='Name'> {props.userData.name}</div>
                <div className='Email'> {props.userData.email}</div>
                <div className='Address'> {props.userData.address}</div>
                <div className='City'> {props.userData.city}</div>
                <div className='State'>{props.userData.state} </div>
                <div className='phone'> {props.userData.phoneno}</div>
                </div>)
}


