import React from 'react';
import './item.css';
import loc from '../../../static/loc.png';
import st from '../../../static/st.png';
import LiveHelpIcon from '@material-ui/icons/LiveHelp';
import EventAvailableRoundedIcon from '@material-ui/icons/EventAvailableRounded';
import Button from '@material-ui/core/Button';
import { green} from '@material-ui/core/colors';
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import LazyLoad from 'react-lazy-load';
import Divider from '@material-ui/core/Divider';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import FavoriteIcon from '@material-ui/icons/Favorite';
import { useMediaQuery } from 'react-responsive';

  


const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(2),
      padding: theme.spacing(1),
      width : '100%',
      height: 250,
      float : 'right',
    },
  },
  filter : {
    display: 'flex',
    '& > *' : {
      width : '100%',
      height : 400

    },
  },

   rentBtn : {
      background :green,
    },

   BoxItemRoot: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(.5),
      padding: theme.spacing(1),
      width : '100%',
      height: 200,
      float : 'right',
      position : 'relative'
    },
  },

}));


const Rentstyle = {
  background: 'green',
};

function ItemMobile(props) {

  const classes = useStyles();
  const Items =  props.items;

  return(
    <div className={classes.BoxItemRoot}>
    <Paper variant="outlined">
    <div className='wishlistMob'> <FavoriteBorderIcon/> </div>
     <div className='row no-gutters'>
      <div className='col-5'>
        <div className='itemImgMob'>
              <div className="filler" >

        <LazyLoad height={130} offsetVertical={120}>
         <img src={("https://picsum.photos/seed/"+Items.brand+"/130/120")} alt=''/>
        </LazyLoad>
         </div>

        </div>
        <div className='itemRentBtnMob'>
          <Button variant="contained" className='rentBtnMob' disableElevation styles={{Rentstyle}}> <div className='itemRentBtnLabelMob'>Rent Now</div></Button>
        </div>
      </div>
      <div className='col-7'>
        <div className='itemBodyMob'>
          <div className='itemTitleMob'>
          {Items.title}
          </div>
          <div className='itemBrandMob'>
          {Items.brand}
          </div>
          <Divider/>
          <div className='itemPriceMob'>

           <div className="style-1">
            <del>
              <span className="cutOffPriceMob">{Items.cutOffPrice}</span>
            </del>
            <ins>
              <span className="priceMob">{Items.price}/<sub>hr</sub></span>
            </ins>
          </div>
                
          
          </div>
          <div className='itemAvailabilityMob'>
          {Items.available==="1" ? <div className='availableMob'><div className='availableMobIcon'><EventAvailableRoundedIcon/></div> <span className='availabletxtMob'>Available</span></div> :<div  className='btn btnMob'><div className='checkIconMob'><LiveHelpIcon/></div>Check</div>}
          </div>

        </div>
      </div>
     </div>

     <div className='itemfooterAddrress'>
     {Items.city}
     </div>
    </Paper>


    </div>
    )
}


function ItemDesktop(props) {
  const Items =  props.items;
  const classes = useStyles();
 
  return (
    <div className='itemRoot'>
     <div className={classes.root}>
      <LazyLoad>
    <Paper variant="outlined">
    <div className='Item'>
        <div className='row'>
         <div className='col-4'>


         <div className='row'>
         <div className='col-9'>
            <div className='row itemImage'> 
          <div className="fillerMain" >
    <LazyLoad height={150} offsetVertical={150}>
     <img src={("https://picsum.photos/seed/"+Items.brand+"/150")} alt=''/>
    </LazyLoad>
     </div>
            </div>
            <div className='row rentbtn'>
           {Items.available ?  <Button variant="contained" disableElevation style={Rentstyle}><div className='rent'> Rent Now</div></Button> :null}
          </div>
            </div> 


         <div className='col-3'>
         <div className='thumbnailgroup'>

          <div className="fillerThumbnail" >
    <LazyLoad height={50} offsetVertical={50}>
     <img src={("https://picsum.photos/seed/"+Items.title+"/50")} alt=''/>
    </LazyLoad>
     </div>
        
               <div className="fillerThumbnail" >
    <LazyLoad height={50} offsetVertical={50}>
     <img src={("https://picsum.photos/seed/"+Items.city+"/50")} alt=''/>
    </LazyLoad>
     </div>

                <div className="fillerThumbnail">
    <LazyLoad height={50} offsetVertical={50}>
     <img src={("https://picsum.photos/seed/"+Items.state+"/50")} alt=''/>
    </LazyLoad>
    </div>
         </div>
         </div>



          </div>
        
          
          


          </div>




         <div className='col-8'>

         <div className='row upper'>
         <div className='col-6'>
      <div className='title'>{Items.title}  </div>
       <div className='brand'> {Items.brand}</div>
       </div>
       <div className='col-6'>
      
      <div className='pricebox'>   <div className="style-1">
  <del>
    <span className="cutOffPrice">{Items.cutOffPrice}</span>
  </del>
  <ins>
    <span className="price">{Items.price}/<sub>hr</sub></span>
  </ins>
</div></div>
       </div>
       </div>

<Divider variant="middle" />


 <div className='row lower'>

 <div className='col-6'>
           <img src={st} alt='' className='st'/>    <div className='descriptionlabel'> {Items.state}</div> 
           <img src={loc} alt='' className='loc'/>   <div className='description'>{Items.city}</div> 
 </div>
 

          <div className='col-6'>
          <div className='btnbox'>
        
         {Items.available ? <div className='available'><EventAvailableRoundedIcon/> Available</div> :<a href='c' className='btn check'><LiveHelpIcon/> Check availablity</a>}
           </div>
       
          </div>
</div>




          </div>

        </div>

    </div>
    </Paper>
     </LazyLoad>
    </div>
    </div>
  );
}


function Item(props){

  const isDesktopOrLaptop = useMediaQuery({
    query: '(min-device-width: 1224px)'
  });
  const isBigScreen = useMediaQuery({ query: '(min-device-width: 1824px)' });
  const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' });
  const isTabletOrMobileDevice = useMediaQuery({
    query: '(max-device-width: 1224px)'
  });
  
  return(<div>
        

      {isDesktopOrLaptop && !isTabletOrMobile ? <ItemDesktop items={props.items}/> : null}
      {isTabletOrMobile || isTabletOrMobileDevice ? <ItemMobile items={props.items}/> : null}
       
        
        
       
        
        
          </div>)


}

export {ItemMobile};
export default Item;