import React from 'react';
import './Jumbo.css';
import search from '../../../static/search.png';
import {MyContext} from '../../context/context';
import axios from 'axios';
import { Link, Element , Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import Loader from 'react-loader-spinner';

class Jumbo extends React.Component {

static contextType = MyContext;
	constructor(props) {
		super(props);
		this.state = {
			search : '',
			disabled :false,
			disabledClass : '',
		}
		this.search = this.search.bind(this);
	}


componentDidMount() {
	 Events.scrollEvent.register('begin', function(to, element) {
      console.log("begin", arguments);
    });
 
    Events.scrollEvent.register('end', function(to, element) {
      console.log("end", arguments);
    });
 
    scrollSpy.update();
}

 componentWillUnmount() {
    Events.scrollEvent.remove('begin');
    Events.scrollEvent.remove('end');
  }

  scrollTo() {
    scroll.scrollTo(250);
  }

handleChange(event) {
	this.setState({search : event.target.value});
}

search(keyword) {
	let query = '';
	let that = this;
	if(this.context.state.isLoggedIn) {
		query = "?search&state=uttarakhand&cnt="+keyword+"&uniqueid="+this.context.state.uniqueid+"&username="+this.context.state.username;
	}
	axios.get('https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php'+query)
	.then(function (response) {
			if(response.data.success) {
				that.setState({'items' : response.data.items, loading : false,
					disabled:false,
					disabledClass : ' '
			},()=>{that.context.searched(that.state.items)
					that.scrollTo();
			})
			}
			else {
				console.log("error",response);
				that.setState({loading : false,disabledClass : ' ',disabled:false});
			}
	})
}

handleSubmit(event) {
    document.getElementById("search").blur();
	this.setState({'disabled' : true});
	this.setState({'disabledClass' : 'disabled'});
	this.setState({loading : true});
	event.preventDefault();
	this.search(this.state.search);

}

render()
{

	return(

            <div className='Jumbo' id="blur-hack">
           <div className='SearchBox'>
          
<center>
<div className='searchdiv'>
<form onSubmit={this.handleSubmit.bind(this)} disabled={this.state.disabled}> 
    <input id='search' className='search' type='text' value={this.state.search} onChange={this.handleChange.bind(this)} placeholder='Search City or Vehicle'/>
          <img src={search} alt='' id='searchlogo' className={this.state.disabledClass} onClick={this.handleSubmit.bind(this)}/>
         </form>
         {this.state.loading && <Loader type="Bars" color="white" height={40} width={40} />}
       </div>  
    
</center>

           </div>
           </div>
		)
}
}

export default Jumbo;